@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

:root {
  --web-color: #e4bb9e;
  --pink-color: #ffe5d9c0;
  --grey-color: #f3f3f3;
  --white-color: #fff;
  --black-color: #70421b;
  --light-black-color: #717171;
  --transition: all 300ms ease-in-out;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

.App {
  padding-top: 100vh; 
}

body {
  font-size: 1.6rem;
  line-height: 1.7;
  font-family: "Poppins", sans-serif;
  color: var(--black-color);
  cursor: url("./images/cursor.png"), auto;
}

a:hover,
button:hover {
  cursor: url("./images/pointer.png"), auto;
}

input:hover,
textarea:hover {
  cursor: url("./images/text-cursor.png"), auto;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: var(--black-color);
}

button {
  outline: 0;
  border: none;
  font-family: inherit;
  background-color: transparent;
  cursor: pointer;
}

img {
  width: 100%;
  display: block;
}

input {
  border: none;
  outline: 0;
  font-family: inherit;
  font-size: 1.6rem;
}

.resize-animation-stopper * {
  animation: none !important;
  transition: none !important;
}

.bg-pink {
  background-color: var(--pink-color);
}
.bg-purple {
  background-color: var(--web-color);
}
.bg-white {
  background-color: var(--white-color);
}

.text-black {
  color: var(--black-color);
}
.text-white {
  color: var(--white-color);
}
.text-purple {
  color: var(--black-color);
}
.text-light-black {
  color: var(--light-black-color);
}
.text-uppercase {
  text-transform: uppercase;
}
.text-center {
  text-align: center;
}
.text-capitalize {
  text-transform: capitalize;
}
.text-italic {
  font-style: italic;
}

.fw-3 {
  font-weight: 300;
}
.fw-4 {
  font-weight: 400;
}
.fw-5 {
  font-weight: 500;
}
fw-6 {
  font-weight: 600;
}
.fw-7 {
  font-weight: 700;
}
.fw-8 {
  font-weight: 800;
}
.fw-9 {
  font-weight: 900;
}

.fs-15 {
  font-size: 1.5rem;
}
.fs-16 {
  font-size: 1.6rem;
}
.fs-17 {
  font-size: 1.7rem;
}
.fs-18 {
  font-size: 1.8rem;
}
.fs-20 {
  font-size: 2rem;
}
.fs-22 {
  font-size: 2.2rem;
}
.fs-24 {
  font-size: 2.4rem;
}
.fs-26 {
  font-size: 2.6rem;
}

.ls-1 {
  letter-spacing: 1px;
}
.ls-2 {
  letter-spacing: 2px;
}

.flex {
  display: flex;
  align-items: center;
}

.flex-column {
  flex-direction: column;
}

.flex-sb {
  justify-content: space-between;
}

.flex-c {
  justify-content: center;
}

.grid {
  display: grid;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.section-title h2 {
  font-weight: 600;
  text-transform: uppercase;
  padding: 18px 0 24px 0;
}

/* Add this at the end of your index.css file */
@media screen and (max-width: 768px) {
  html {
    font-size: 80%; /* This will make all rem-based fonts 80% of their original size */
  }
}

@media screen and (max-width: 480px) {
  html {
    font-size: 52%; /* Even smaller for mobile phones */
  }
}